import './App.scss';
import {Routes, Route} from "react-router-dom";
import Layout from "./components/Layout";
import Project from "./components/Project";
import projectdata from "./data/projectdata";

function createRoute(project)
{
  var url = "/project-" + project.category + "-" + project.id;
  return (
    <Route exact path={url} element={<Project id={project.id} title={project.title} description={project.description}/>}/>
  )
}

function App() {
  return (
    <Routes>
      {projectdata.map(createRoute)}
      <Route exact path="/" element={<Layout/>}></Route>

    </Routes>
  )
}

export default App;
